import { Injectable } from '@angular/core';
let localLayout: any = null;
@Injectable()
export class CommonService {
  public dateFormat = function (fmt: any, date: any) {
    let ret;
    const opt: any = {
      'Y+': date.getFullYear().toString(),
      'm+': (date.getMonth() + 1).toString(),
      'd+': date.getDate().toString(),
      'H+': date.getHours().toString(),
      'M+': date.getMinutes().toString(),
      'S+': date.getSeconds().toString(),
    };
    for (let k in opt) {
      ret = new RegExp('(' + k + ')').exec(fmt);
      if (ret) {
        fmt = fmt.replace(
          ret[1],
          ret[1].length == 1 ? opt[k] : opt[k].padStart(ret[1].length, '0')
        );
      }
    }
    return fmt;
  }

  public dateFormatNew = function (type: any, now: any) {
    // var now = new Date(),
    let y = now.getFullYear();
    let m = ('0' + (now.getMonth() + 1)).slice(-2);
    let d = ('0' + now.getDate()).slice(-2);
    if (type == 'Y-mm-dd 00:00:00') {
      let temp = y + '/' + m + '/' + d + ' 00:00:00';
      temp = temp.replace(/-/g, '/');
      return temp;
    } else {
      let temp = y + '/' + m + '/' + d + ' 23:59:59';
      temp = temp.replace(/-/g, '/');
      return temp;
    }
  }

  public buildEChartLabel(params: string, n?: number, agrs?: Array<any>) {
    if (!params) {
      return;
    }
    let newParamsName = ''; // 最终拼接成的字符串
    let paramsNameNumber = params.length; // 实际标签的个数
    const provideNumber = n || 4; // 每行能显示的字的个数
    const rowNumber = Math.ceil(paramsNameNumber / provideNumber); // 换行的话，需要显示几行，向上取整
    /**
     * 判断标签的个数是否大于规定的个数， 如果大于，则进行换行处理 如果不大于，即等于或小于，就返回原标签
     */
    // 条件等同于rowNumber>1
    if (paramsNameNumber > provideNumber) {
      /** 循环每一行,p表示行 */
      for (var p = 0; p < rowNumber; p++) {
        var tempStr = ''; // 表示每一次截取的字符串
        var start = p * provideNumber; // 开始截取的位置
        var end = start + provideNumber; // 结束截取的位置
        // 此处特殊处理最后一行的索引值
        if (p == rowNumber - 1) {
          // 最后一次不换行
          tempStr = params.substring(start, paramsNameNumber);
        } else {
          // 每一次拼接字符串并换行
          tempStr = params.substring(start, end) + '\n';
        }
        newParamsName += tempStr; // 最终拼成的字符串
      }
    } else {
      // 将旧标签的值赋给新标签
      newParamsName = params;
    }
    //将最终的字符串返回
    let argsStr = '';
    agrs?.forEach((item: string) => {
      argsStr += '\n' + item;
    });
    return newParamsName + argsStr;
  }

  public genclass = function (url: string) {
    if (url == '' || url == null) {
      return '';
    } else {
      let urlarr = url.toLowerCase().split('.');
      let ext = urlarr[urlarr.length - 1];
      let logotype = '';
      switch (ext) {
        case 'xls':
          logotype = 'fa fa-file-excel fa-4x green';
          break;
        case 'xlsx':
          logotype = 'fa fa-file-excel fa-4x green';
          break;
        case 'doc':
          logotype = 'fa fa-file-word fa-4x blue';
          break;
        case 'docx':
          logotype = 'fa fa-file-word fa-4x blue';
          break;
        case 'ppt':
          logotype = 'fa fa-file-powerpoint fa-4x red';
          break;
        case 'pptx':
          logotype = 'fa fa-file-powerpoint fa-4x red';
          break;
        case 'ppt':
          logotype = 'fa fa-file-pdf fa-4x red';
          break;
        case 'pdf':
          logotype = 'fa fa-file-pdf fa-4x red';
          break;
        default:
          logotype = 'fa fa-file fa-4x';
      }
      return logotype;
    }
  }

  public isInvalid = function (obj: any, list: Array<any>): boolean {
    for (let i = 0; i < list.length; i++) {
      if (list[i] in obj && !obj[list[i]]) {
        return true;
      }
    }

    return false;
  }

  public getNextDate = function (date: any, day = 1, format = '{y}-{m}-{d}') {
    if (date) {
      const nDate = new Date(date);
      nDate.setDate(nDate.getDate() + day);

      const formatObj: any = {
        y: nDate.getFullYear(),
        m: nDate.getMonth() + 1,
        d: nDate.getDate(),
      };
      return format.replace(/{([ymd])+}/g, (result, key) => {
        const value = formatObj[key];
        return value.toString().padStart(2, '0');
      });
    } else {
      console.log('date can not be null');
      return;
    }
  }

  public second2Hour = function (value: number) {
    //  秒
    let second: number = value;
    //  分
    let minute = 0;
    //  小时
    let hour = 0;
    //  天
    //  let day = 0
    //  如果秒数大于60，将秒数转换成整数
    if (second > 60) {
      //  获取分钟，除以60取整数，得到整数分钟
      minute = parseInt((second / 60).toString());
      //  获取秒数，秒数取佘，得到整数秒数
      second = parseInt((second % 60).toString());
      //  如果分钟大于60，将分钟转换成小时
      if (minute > 60) {
        //  获取小时，获取分钟除以60，得到整数小时
        hour = parseInt((minute / 60).toString());
        //  获取小时后取佘的分，获取分钟除以60取佘的分
        minute = parseInt((minute % 60).toString());
        //  如果小时大于24，将小时转换成天
        //  if (hour > 23) {
        //    //  获取天数，获取小时除以24，得到整天数
        //    day = parseInt(hour / 24)
        //    //  获取天数后取余的小时，获取小时除以24取余的小时
        //    hour = parseInt(hour % 24)
        //  }
      }
    }

    let result = '' + parseInt(second.toString()) + 'S';
    if (minute > 0) {
      result = '' + parseInt(minute.toString()) + 'M' + result;
    }
    if (hour > 0) {
      result = '' + parseInt(hour.toString()) + 'H' + result;
    }
    //  if (day > 0) {
    //    result = '' + parseInt(day) + '天' + result
    //  }
    return result;
  }

  /**
   * Array transform to tree
   * @param {array} data
   * @param {string} id
   */
  // 数组转换成tree
  public arrayToTree = function (data: any, id: string) {
    const result: any[] = [];
    if (!Array.isArray(data)) {
      return result;
    }
    const map = {};
    data.forEach(item => {
      delete item.children;
      // @ts-ignore
      map[item[id]] = item;
    });

    data.forEach(item => {
      // @ts-ignore
      const parent = map[item.parentId] || map[item.parentCode];

      if (parent) {
        (parent.children || (parent.children = [])).push(item);
      } else {
        result.push(item);
      }
    });
    return result;
  }

  /**
   * Tree transform to array
   * @param tree
   */
  // Tree to array
  public treeToArray = function (tree: any) {
    const arr: any[] = [];
    const expanded = (datas: any) => {
      if (datas && datas.length > 0) {
        datas.forEach((e: any) => {
          arr.push(e);
          expanded(e.children);
        });
      }
    };
    expanded(tree);
    return arr;
  }

  public printNewWindow = function (imgSrc: string, option?: any) {
    let oWin: any = window.open(
      '',
      'pringwindow',
      'menubar=no,location=no,resizable=yes,scrollbars=no,status=no,width=' +
      window.screen.availWidth +
      ',height=' +
      window.screen.availHeight
    );
    oWin.document.fn = function () {
      if (oWin) {
        oWin.print();
        oWin.close();
      }
    };
    let title = '';
    if (option && option.title) {
      title =
        '<h3 style="width: 100%;text-align: center;margin: 100px 0;font-size: 24px;">' +
        option.title +
        '</h3>';
    }
    let html =
      '<div style="height: 100%;width: 100%;display: flex;flex-direction: column;align-items: center">' +
      title +
      `<img src="${imgSrc}" onload="fn()" style="width: 90%;" />` +
      '</div>' +
      '<style type="text/css">' +
      '@page { margin: 0; }' +
      '</style>';
    oWin.document.open();
    oWin.document.write(html);
    oWin.document.close();
  }

  getHourByMinute = function (minute: number) {
    return Math.trunc(minute / 60);
  }

  public formInvalid = function (obj: any, list: Array<any>): boolean {
    for (let i = 0; i < list.length; i++) {
      if (list[i] in obj && !obj[list[i]]) {
        return list[i];
      } else if (
        Object.prototype.toString.call(obj[list[i]]) === '[object Array]' &&
        obj[list[i]].length === 0
      ) {
        return list[i];
      }
    }

    return false;
  }

  public getBase64 = (file: File): Promise<string | ArrayBuffer | null> =>
    new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = error => reject(error);
    });

  public downloadFile = function (urlStr: string, fileName: string) {
    const link = document.createElement('a');
    const url = '/gateway/mfgtranning/api/mfg/Common/file?objectName=' + urlStr;
    link.style.display = 'none';
    link.href = url;
    link.setAttribute('download', fileName);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }

  public optionsValid = function (optionList: Array<any>, type: any, messageService: any, TranslateData: any) {
    const optionCount = optionList.length;
    let YCount = 0;

    if (optionCount === 0) {
      messageService.add({ severity: 'warn', summary: TranslateData.questionOption1Warn });

      return false;
    }

    optionList.forEach(item => {
      if (item.answer) {
        YCount++;
      }
    });

    if (type === '1') {
      //  判断题
      if (optionCount !== 2) {
        messageService.add({
          severity: 'warn',
          summary: TranslateData.questionOption2Warn,
        });
        return false;
      }

      if (YCount !== 1) {
        messageService.add({
          severity: 'warn',
          summary: TranslateData.questionOption3Warn,
        });
        return false;
      }
    } else if (type === '2') {
      if (optionCount < 3) {
        messageService.add({
          severity: 'warn',
          summary: TranslateData.questionOption4Warn,
        });
        return false;
      }

      if (YCount !== 1) {
        messageService.add({
          severity: 'warn',
          summary: TranslateData.questionOption5Warn,
        });
        return false;
      }
    } else if (type === '3') {
      // 多选
      if (optionCount < 3) {
        messageService.add({
          severity: 'warn',
          summary: TranslateData.questionOption4Warn,
        });
        return false;
      }

      if (YCount <= 1) {
        messageService.add({
          severity: 'warn',
          summary: TranslateData.questionOption6Warn,
        });
        return false;
      }
    }
    return true;
  }

  public translateData = function (Obj: any, translate: any) {
    for (let item in Obj) {
      if (Obj.hasOwnProperty(item)) {
        translate.get(Obj[item]).subscribe((data: string) => {
          Obj[item] = data;
        });
      }
    }
  }

  public imgToBase64 = function (imgSrc: string) {
    const canvas: any = document.createElement('CANVAS'),
      ctx = canvas.getContext('2d'),
      img = new Image;
    img.crossOrigin = 'Anonymous';
    img.src = imgSrc;
    img.onload = function () {
      canvas.height = img.height;
      canvas.width = img.width;
      ctx.drawImage(img, 0, 0);
      const dataURL = canvas.toDataURL('image/png');
      // me.floodImage = blob.replace('data:image/png;base64,', '');
    };
  }

  public setFullScreen = function (exitFullScreen?: boolean) {
    if (localLayout) {
      localLayout.fullPage = !exitFullScreen
    }

    window?.parent?.postMessage({
      type: 'jabil-bus-full-screen', data: { exitFullScreen: exitFullScreen }
    }, '*')
  }

  public addRouteEvent(router: any) {
    if (!router?.navigate) {
      console.error('jabil-bus-lib param error: router.navigate is undefined')
      return
    }
    window.addEventListener('updateAppUrl', (e: any) => {
      router.navigate([e.detail.path]);
    });
  }

  public logoff = function () {
    const removeItems = [
      'loginCheck',
      'loginType',
      'selectedIndex',
      'jwt',
      'tabs',
      'tabMenu',
      'region',
      'currentRegion',
      'username',
      'defaultUrl',
      'roles',
      'translateApp',
      'zh',
      'en'
    ];
    removeItems.forEach((item: string) => {
      localStorage.removeItem(item);
    });

    const langCacheKey = localStorage.getItem('langCacheKey');
    localStorage.removeItem('' + langCacheKey);
    window.parent.location.reload()
  }

  public getToken = function () {
    return localStorage.getItem('jwt')
  }

  saveLayout(layout: any) {
    localLayout = layout
  }

  getGuid(): string {
    return ('' + [1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, ch => {
      let c = Number(ch);
      return (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16)
    }
    )
  }

  initWebNotice(signalR: any) {
    console.log(signalR)

    const isHttps = document.location.protocol === 'https:';
    const testWS = 'http://cnhuam0itstg83:44023/modelstatus-statistics-messaging-hub';
    const prodWS = 'https://jabilbus.jblapps.com/ws/occomain/modelstatus-statistics-messaging-hub';
    const wsBayUrl = isHttps ? prodWS : testWS;

    const connection = new signalR.HubConnectionBuilder()
      .withUrl(wsBayUrl)
      .configureLogging(signalR.LogLevel.Information)
      .build();

    connection.on('ReceiveMessageAsync', (data: any) => {
      this.reciveWebNotice(data)
      if (data?.messageBody) {
        console.log(data?.messageBody);
      }
    });

    const start = async () => {
      try {
        await connection.start();
        console.log('SignalR Connected.');
      } catch (err) {
        console.log(err);
        setTimeout(start, 5000);
      }
    };

    connection.onclose(async () => {
      await start();
    });

    // Start the connection.
    start();
  }

  reciveWebNotice(data: any): any {
    let options: any = null
    var PERMISSON_GRANTED = "granted";
    var PERMISSON_DENIED = "denied";
    var PERMISSON_DEFAULT = "default";

    console.log(document.location)
    // @ts-ignore
    window.notices.forEach((item: any) => {
      // 判断key是否相同
      if (item) {
        options = item
      }
    });

    if (!options) {
      return
    }

    // 如果用户已经允许，直接显示消息，如果不允许则提示用户授权
    if (Notification.permission === PERMISSON_GRANTED) {
      this.notify(options);
    } else {
      Notification.requestPermission((res) => {
        if (res === PERMISSON_GRANTED) {
          this.notify(options);
        }
      });
    }
  }

  private notify($options: any, callback?: Function) {
    var notification = new Notification($options?.title, $options);

    notification.onshow = (event) => {
      console.log("show : ", event);
    };

    notification.onclose = (event) => {
      console.log("close : ", event);
    };

    notification.onclick = (event: any) => {
      console.log("click : ", event);
      // 当点击事件触发，打开指定的url
      event?.target?.data && window.open(event?.target?.data)
      notification.close();
    };
  }

  registerNotice(options: any) {
    // @ts-ignore
    window.notices = []
    // @ts-ignore
    window.notices.push(options)
  }
}
