import { Pipe, PipeTransform } from '@angular/core';
// import distanceInWordsToNow from 'date-fns/distance_in_words_to_now';
// import dateFns from 'date-fns';

@Pipe({
  name: 'SecondToHour',
})
export class SecondToHour implements PipeTransform {
  /**
   * Takes a value and makes it lowercase.
   */
  transform(value: number, ...args: Array<any>) {
    if (!value) {
      return '';
    }

    //  秒
    let second: number = value;
    //  分
    let minute = 0;
    //  小时
    let hour = 0;
    //  天
    //  let day = 0
    //  如果秒数大于60，将秒数转换成整数
    if (second > 60) {
      //  获取分钟，除以60取整数，得到整数分钟
      minute = parseInt((second / 60).toString());
      //  获取秒数，秒数取佘，得到整数秒数
      second = parseInt((second % 60).toString());
      //  如果分钟大于60，将分钟转换成小时
      if (minute > 60) {
        //  获取小时，获取分钟除以60，得到整数小时
        hour = parseInt((minute / 60).toString());
        //  获取小时后取佘的分，获取分钟除以60取佘的分
        minute = parseInt((minute % 60).toString());
        //  如果小时大于24，将小时转换成天
        //  if (hour > 23) {
        //    //  获取天数，获取小时除以24，得到整天数
        //    day = parseInt(hour / 24)
        //    //  获取天数后取余的小时，获取小时除以24取余的小时
        //    hour = parseInt(hour % 24)
        //  }
      }
    }

    let result = '' + parseInt(second.toString()) + 'S';
    if (minute > 0) {
      result = '' + parseInt(minute.toString()) + 'M' + result;
    }
    if (hour > 0) {
      result = '' + parseInt(hour.toString()) + 'H' + result;
    }
    //  if (day > 0) {
    //    result = '' + parseInt(day) + '天' + result
    //  }
    return result;
  }
}
