import { Injectable } from '@angular/core';

let heartTime!: any;
let reConnectTimeOut!: any;

@Injectable()
export class WebsocketService {
  constructor() {}

  init(url: string, params: any, resFunc: any, isReConnect?: boolean) {
    if (!url) {
      return;
    }
    let webSocket!: any;
    let noReConnect: boolean = false;
    webSocket = new WebSocket(url);
    webSocket.onmessage = async (res: any) => {
      const data = JSON.parse(res.data);
      resFunc(data);
      // this.buildData(data.MessageBody, params, resFunc);
    };
    webSocket.onopen = () => {
      console.log('webSocket connected');
      heartTime = setInterval(() => {
        // onmessage
        console.log('ping!');
        webSocket.send('ping');
      }, 20000);
      // webSocket.send(JSON.stringify(params));
    };
    webSocket.onclose = () => {
      if (isReConnect && !webSocket.noReConnect) {
        this.socketReConnect(url, params, resFunc, isReConnect);
      }
      heartTime && clearInterval(heartTime);
      console.log('webSocket closed');
    };
    webSocket.onerror = (res: any) => {
      if (isReConnect && webSocket.noReConnect) {
        this.socketReConnect(url, params, resFunc, isReConnect);
      }
      heartTime && clearInterval(heartTime);
      console.error(res);
    };

    return webSocket;
  }

  private socketReConnect(url: string, params: any, resFunc: any, isReConnect?: boolean) {
    // reconnect time 5s
    reConnectTimeOut && clearTimeout(reConnectTimeOut);
    reConnectTimeOut = setTimeout(() => {
      this.init(url, params, resFunc, isReConnect);
    }, 5000);
  }
}
