import { TranslateLoader } from '@ngx-translate/core';
import { Observable } from 'rxjs';
import { TranslationApiService } from './translation.service';

export class TranslationApiLoader implements TranslateLoader {
  constructor(private api: TranslationApiService, private translateJson?: object ) {}

  public getTranslation(lang: string): any {
    return this.api.getTranslationsFromApi(lang, this.translateJson) as Observable<any>;
  }
}
