import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { environment } from '../../../environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root',
})
export class AppInfoService {
  private _domainUri: string = environment.i18nDomainUri;
  private _apiKey: string = environment.i18nApiKey;
  private application: any | undefined;

  constructor(private http: HttpClient) {}

  public getAppInfo(): Observable<Object> {
    const getAppInfoProxyUri = `${this._domainUri}/api/applications-info`;
    return this.getData(getAppInfoProxyUri, {}) as Observable<Object>;
  }

  public getData(route: string, httpOptions: any) {
    let headers = httpOptions.headers as HttpHeaders;
    if (headers) {
      headers.append('x-api-key', this._apiKey);
    } else {
      httpOptions.headers = { 'x-api-key': this._apiKey };
    }

    return this.http.get(route, httpOptions);
  }

  public getCacheData(key: string) {
    let langJson = localStorage.getItem(key);
    if (langJson) return new BehaviorSubject(langJson);

    return new BehaviorSubject('');
  }

  setApp(val: any) {
    this.application = val;
  }

  getApp() {
    return this.application;
  }
}
