/**
 * name:http service
 * describe: http common module
 */
import { Injectable } from '@angular/core';
import { HttpClient, HttpRequest, HttpHeaders } from '@angular/common/http';
import { MessageService } from 'primeng/api';
import { LoadingComponent } from 'src/app/components/loading/loading.component';

let self: any = {};
let isDownload: boolean = false;
let selfName: string = '';

@Injectable()
export class HttpService {
  self: any = {};

  constructor(private http: HttpClient) {
    self = this;
  }

  /**
   * request
   * @param params
   * @param header
   * @returns {Promise<{success: boolean,msg: string}>|Promise<R>}
   */
  public request(params: any, header?: any): any {
    isDownload = false;
    const method = params['method'].toLowerCase();
    if (method === 'post') {
      return this.post(params['url'], params['data'], header);
    } else if (method === 'delete') {
      return this.delete(params['url'], params['data'], header);
    } else if (method === 'put') {
      return this.put(params['url'], params['data'], header);
    } else if (method === 'download') {
      return this.download(params['url'], params['data'], header);
    } else if (method === 'downloadfile') {
      return this.downloadFile(params['url'], params['data'], header, params['fileName']);
    } else if (method === 'upload') {
      return this.upload(params['url'], params['data'], header);
    } else {
      return this.get(params['url'], params['data'], header);
    }
  }

  /**
   * get
   * @param url
   * @param params
   * @param header
   * @returns {Promise<R>|Promise<U>}
   */
  private get(url: string, params: any, header?: any): any {
    let jwt = localStorage.getItem('jwt');
    let headers: any = { 'Content-Type': 'application/json-patch+json' };
    if (jwt) {
      headers = {
        'Content-Type': 'application/json-patch+json',
        Authorization: 'Bearer ' + jwt,
      };
    }
    if (header) {
      headers = Object.assign(headers, header)
    }

    let options = {
      params,
      headers,
    };

    return this.http
      .get(url, options)
      .toPromise()
      .then(HttpService.handleSuccess)
      .catch(res => HttpService.handleError(res));
  }

  /**
   * post
   * @param url
   * @param params
   * @param header
   * @returns {Promise<R>|Promise<U>}
   */
  private post(url: string, params: any, header?: any) {
    let jwt = localStorage.getItem('jwt');
    let lang = localStorage.getItem('lang');
    let headers: any = { 'Content-Type': 'application/json-patch+json' };
    if (jwt) {
      headers = {
        'Content-Type': 'application/json-patch+json',
        'Accept-Language': lang === 'en' ? 'en' : 'zh-Hans',
        Authorization: 'Bearer ' + jwt,
      };
    }

    if (header) {
      headers = Object.assign(headers, header)
    }

    let options = {
      headers,
    };

    if (headers['Content-Type'] && headers['Content-Type'].includes('form-data')) {
      const formData = params
      return this.http
        .post(url, formData, options)
        .toPromise()
        .then(HttpService.handleSuccess)
        .catch(res => HttpService.handleError(res));
    }

    return this.http
      .post(url, params, options)
      .toPromise()
      .then(HttpService.handleSuccess)
      .catch(res => HttpService.handleError(res));
  }

  /**
   * delete
   * @param url
   * @param params
   * @param header
   * @returns {Promise<R>|Promise<U>}
   */
  private delete(url: string, params: any, header?: any) {
    let jwt = localStorage.getItem('jwt');
    let headers: any = { 'Content-Type': 'application/json-patch+json' };
    if (jwt) {
      headers = {
        'Content-Type': 'application/json-patch+json',
        Authorization: 'Bearer ' + jwt,
      };
    }

    if (header) {
      headers = Object.assign(headers, header)
    }
    let options = {
      body: params,
      headers,
    };
    return this.http
      .delete(url, options)
      .toPromise()
      .then(HttpService.handleSuccess)
      .catch(res => HttpService.handleError(res));
  }

  /**
   * put
   * @param url
   * @param params
   * @param header
   * @returns {Promise<R>|Promise<U>}
   */
  private put(url: string, params: any, header?: any) {
    let jwt = localStorage.getItem('jwt');
    let headers: any = { 'Content-Type': 'application/json-patch+json' };
    if (jwt) {
      headers = {
        'Content-Type': 'application/json-patch+json',
        Authorization: 'Bearer ' + jwt,
      };
    }

    if (header) {
      headers = Object.assign(headers, header)
    }

    let options = {
      headers,
    };
    return this.http
      .put(url, params, options)
      .toPromise()
      .then(HttpService.handleSuccess)
      .catch(res => HttpService.handleError(res));
  }

  /**
   * upload
   * @param url
   * @param params
   * @param header
   * @returns {Promise<R>|Promise<U>}
   */
  private upload(url: string, params: any, header?: any) {
    let jwt = localStorage.getItem('jwt');
    let headers: HttpHeaders = new HttpHeaders();
    headers.append('Content-Type', 'multipart/form-data');
    if (jwt) {
      headers.append('Authorization', 'Bearer ' + jwt);
    }
    let options = {
      headers,
    };

    if (header) {
      headers = Object.assign(headers, header)
    }

    const req = new HttpRequest('POST', url, params, {
      headers: headers,
      reportProgress: true,
    });

    return this.http
      .request(req)
      .toPromise()
      .then(HttpService.handleSuccess)
      .catch(res => HttpService.handleError(res));
  }

  /**
* downloadFile
* @param url
* @param params
* @param type
* @param fileName
* @returns {Promise<R>|Promise<U>}
*/
  private downloadFile(url: string, params: any, type?: string, fileName?: string) {
    selfName = fileName || ''
    isDownload = true;
    let jwt = localStorage.getItem('jwt');
    let headers: HttpHeaders = new HttpHeaders({ Authorization: 'Bearer ' + jwt });

    const req = new HttpRequest(type || 'POST', url, params, {
      headers: headers,
      reportProgress: true,
      responseType: 'blob',
    });

    return this.http
      .request(req)
      .toPromise()
      .then(HttpService.handleSuccess)
      .catch(res => HttpService.handleError(res));
  }

  /**
   * download
   * @param url
   * @param params
   * @param type
   * @returns {Promise<R>|Promise<U>}
   */
  private download(url: string, params: any, type?: string) {
    selfName = ''
    isDownload = true;
    let jwt = localStorage.getItem('jwt');
    let headers: HttpHeaders = new HttpHeaders({ Authorization: 'Bearer ' + jwt });

    const req = new HttpRequest(type || 'POST', url, params, {
      headers: headers,
      reportProgress: true,
      responseType: 'blob',
    });

    return this.http
      .request(req)
      .toPromise()
      .then(HttpService.exportData)
      .catch(res => HttpService.handleError(res));
  }

  private static exportData(res: any) {
    const blob = new Blob([res.body], {
      type: res.body.type,
    });
    const filename = selfName ||
      window.decodeURI(res.headers.get('content-disposition')?.split('=')[1]);
    const link = document.createElement('a');
    const url = URL.createObjectURL(blob);
    link.style.display = 'none';
    link.href = url;
    link.setAttribute('download', filename);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url); // revoke blob object
  }

  /**
   * success response
   * @param res
   * @returns {{data: (string|null|((node:any)=>any)
   */
  private static handleSuccess(res: any) {
    // download
    if (res?.url?.includes('export') || res?.url?.includes('Export') ||
      res?.url?.includes('excel') || res?.url?.includes('Excel') ||
      isDownload) {
      const blob = new Blob([res.body], {
        type: res.body.type,
      });
      const filename = selfName ||
        window.decodeURI(res.headers.get('content-disposition')?.split('=')[1]);
      const link = document.createElement('a');
      const url = URL.createObjectURL(blob);
      link.style.display = 'none';
      link.href = url;
      link.setAttribute('download', filename);
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url); // revoke blob object
      return;
    }
    return res;
  }

  /**
   * error
   * @param error
   * @returns {void|Promise<string>|Promise<T>|any}
   */
  private static handleError(error: any) {
    let msg = 'request error';
    if (error.status == 400) {
      console.log('please check parameter');
    }
    if (error.status == 401) {
      HttpService.logout();
    }
    if (error.status == 403) {
      console.log('No Permission');
      window.dispatchEvent(new CustomEvent('api-403'));
      // const messageService = new MessageService()
      // messageService.add({
      //   severity: 'warn',
      //   summary: 'No Permission',
      // });
    }
    if (error.status == 404) {
      console.error('please check link');
    }
    if (error.status == 500) {
      console.error('please check server');
    }
    return { code: -1, data: null, msg: error, };
  }

  private static logout() {
    localStorage.removeItem('loginCheck');
    localStorage.removeItem('username');
    localStorage.removeItem('selectedIndex');
    localStorage.removeItem('jwt');
    localStorage.removeItem('tabs');
    localStorage.removeItem('tabMenu');
    localStorage.removeItem('region');
    localStorage.removeItem('translateApp');
    localStorage.removeItem('zh');
    localStorage.removeItem('en');
    window.location.reload();
    window.parent.location.reload();
  }
}

