import { Injectable } from '@angular/core';
import { MissingTranslationHandler, MissingTranslationHandlerParams } from '@ngx-translate/core';
import { AppInfoService } from '../app-info/app-info.service';

@Injectable({
  providedIn: 'root',
})
export class MissingTranslationsService implements MissingTranslationHandler {
  constructor(private appInfo: AppInfoService) {}

  handle(params: MissingTranslationHandlerParams) {
    const namespaces = this.appInfo.getApp()?.namespaces;
    let key = `${params.key}`;
    if (namespaces) {
      for (let ns of namespaces) {
        if (key.indexOf(ns.namespace) !== -1) {
          key = key.replace(`${ns.namespace}.`, '');
          break;
        }
      }
    }
    return key;
  }
}
